@import "./../../../generals/root.scss";

#Form {
    height: fit-content !important;
}

.registration {
    text-align: center;

    h2 {
        margin-bottom: 20px;
    }

    & .registrationBtn {
        margin-bottom: 40px;
        margin: 0 auto 20px auto;
        // width: 20vw;
        // height: 6vh;
        padding: 10px 30px;
        font-size: $fsh3;
        // border: 1px solid $darkGreenBackground;
    }

    // & h1 {
    //     margin-bottom: 20px;
    // }

    .ant-collapse {
        border: none;
        width: 60vw;
        margin: 0 auto;
        background-color: rgba(244, 244, 244, 0.3);

        & .ant-collapse-item {
            // margin-bottom: 5px;
            &.ant-collapse-item-active {
                background-color: rgba(244, 244, 244, 0.8);
                & .ant-collapse-header-text {
                    font-weight: bold;
                }
                & .ant-collapse-header {
                    & .ant-collapse-expand-icon {
                        transform: rotate(180deg);
                    }
                }
            }

            & .ant-collapse-header {
                border: 1px solid rgb(219, 219, 219);
                border: none;
                border-radius: 0;
                text-align: left;
                display: flex;
                flex-direction: row-reverse;

                & .ant-collapse-expand-icon {
                    transform: rotate(90deg);
                }
            }

            & .ant-collapse-content {
                border: 1px solid rgba(198, 193, 193, 0.24);
                border-radius: 0;
            }
        }
    }
}
