.treeBlock {
    height: 100%;


    & > div {
        text-align: center;
        box-shadow: 0 1px 2px -2px rgb(0 0 0 / 6%), 0 3px 6px 0 rgb(0 0 0 / 4%), 0 5px 12px 4px rgb(0 0 0 / 2%);
        background-color: rgba(41, 40, 40, 0.5);
        border-radius:10px;
        padding: 10px;
        margin-bottom: 10px;

        & .ant-steps.ant-steps-horizontal {
            margin-bottom: 0 !important;
        }
        h1 {
            margin-bottom: 20px;
        }
    }
}
