    & h1 {
        margin-bottom: 20px;
        font-size: 25px;
        font-weight: bold;
    }

    .sessions {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    & h1 {
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: bold;
    }

    .test {
        @for $i from 1 through 6 {
            $color: $i * 15;
            & li:nth-of-type(#{$i}) div {
                background-color: rgb($color, $color, $color);
            }
            & li:nth-of-type(#{$i}) div::after {
                border-left: 25px solid rgb($color, $color, $color);
            }
        }

        & li {
            width: fit-content;

            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            border-bottom: 1px solid #d9d9d9;
            margin-bottom: 5px;
            border-radius: 25px;

            & div {
                width: 40px;
                height: 30px;
                margin-right: 35px;
                // background-color: black;
                display: flex;
                color: white;
                align-items: center;
                justify-content: center;
                font-size: 18px;
                font-weight: bold;
                position: relative;

                &::after {
                    content: "";
                    position: absolute;
                    left: 40px;
                    bottom: 0px;
                    border: 15px solid transparent;
                    border-left: 25px solid black;
                }
            }
            & span {
                padding: 0 10px 0 0;
            }
        }
    }
