@import "../../../generals/root.scss";

#Information {
    height: fit-content !important;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.paralaxImg {
    height: 49vh;
    background-image: url("../../../assets/img/dates.jpg");
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    box-shadow: 0px 5px 45px -9px rgb(75, 117, 157);
    & * {
        color: $whiteColorText !important;
    }
}

.aboutUs {
    height: fit-content !important;
    color: $blackColorText;
    display: grid;
    grid-template-columns: 4fr 1fr;
    gap: 20px;
    margin-bottom: 20px;

    & .leftText {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        width: 100%;
        height: 100%;
        padding: 30px;
        border-radius: 10px;
        box-shadow: 0 1px 2px -2px rgb(0 0 0 / 6%), 0 3px 6px 0 rgb(0 0 0 / 4%), 0 5px 12px 4px rgb(0 0 0 / 2%);

        & h1 {
            font-size: $fsh2;
            margin-bottom: 15px;
        }
        & p {
            font-size: $fsMainText;
            margin-bottom: 20px;
        }
    }
    & .rightPoster {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 15px;
        height: 100%;

        &  div {
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            border-radius: 10px;
            box-shadow: 0 1px 2px -2px rgb(0 0 0 / 6%), 0 3px 6px 0 rgb(0 0 0 / 4%), 0 5px 12px 4px rgb(0 0 0 / 2%);

            &:hover {
                box-shadow: 0 1px 2px -2px rgb(0 0 0 / 7%), 0 3px 6px 0 rgb(0 0 0 / 5%), 0 5px 12px 4px rgb(0 0 0 / 3%);
            }

            & > div {
                min-height: 100px;
                width: 100%;
                background-image: url("../../../assets/img/speaking.png");
                background-position: center center;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }
}
