#OrganizingCommittee {
    height: fit-content !important;
    text-align: center;

    & h1 {
        margin-bottom: 20px;
    }

    & .orgContent {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;

        & ul {
            text-align: start;
            list-style-type: none;

            @for $i from 1 through 12 {
                $color: $i * 15;
                & li:nth-of-type(#{$i}) div {
                    background-color: rgb($color, $color, $color);
                }
                & li:nth-of-type(#{$i}) div::after {
                    border-left: 25px solid rgb($color, $color, $color);
                }
            }
            & li {
                width: fit-content;

                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                border-bottom: 1px solid #d9d9d9;
                margin-bottom: 5px;
                border-radius: 25px;

                & div {
                    width: 20px;
                    height: 20px;
                    border-radius: 2px 0 0 2px;
                    margin-right: 35px;
                    // background-color: black;
                    display: flex;
                    color: white;
                    align-items: center;
                    justify-content: center;
                    font-size: 18px;
                    font-weight: bold;
                    position: relative;

                    &::after {
                        content: "";
                        position: absolute;
                        left: 20px;
                        bottom: 0px;
                        border: 10px solid transparent;
                        border-left: 25px solid black;
                    }
                }
                & span {
                    padding: 0 10px 0 0;
                }
            }
        }

        & .testBlock {
            width: 200px;
            height: 250px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.07);

            & .blockImg {
                height: 70%;
                position: relative;

                & .oval {
                    position: absolute;
                    height: 120%;
                    width: 150px;
                    bottom: 10px;
                    left: 25px;
                    border-radius: 50%;
                    background: #d9d9d9;
                }
            }

            & .title {
                display: flex;
                flex-direction: column;

                height: 30%;
                text-align: center;

                & .discription {
                    margin-top: 10px;
                    color: rgba(0, 0, 0, 0.634);
                }
            }
        }
    }

    @media (max-width: 1100px) {
        & .orgContent {
            justify-content: center;

            & .testBlock {
                margin-top: 50px;
            }
        }
    }
}
