@import "../../generals/root.scss";

.navBar {
    
    background-color: $lightGreenBackground;
    // background-color: #001529;

    box-shadow: 10px 0px 8px rgb(0, 0, 0, 15%);

    z-index: 1000;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    figcaption {
        color: $whiteColorText;
    }

    & img {
        width: 100px;
    }

    & li {
        margin-bottom: 20px;
        list-style-type: none;
    }

    & li > a {
        font-size: $fsLinkNavBar;
        color: $notActiveLink;
        text-decoration: none;
        transition: all 0.3s ease-in;

        &.active {
            color: $whiteColorText;
        }
    }
}
