@import "../../../generals/root.scss";

.hero {
    width: 100%;
    height: 100vh;
    position: relative;
    padding: 50px 50px 0 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-image: url("../../../assets/img/headerBackImg.jpg");
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    color: $whiteColorText;

    &__content {
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        h1 {
            font-size: 5em;
            font-weight: bold;
        }
        h2 {
            font-size: 3em;
        }
    }
    .invisib {
        height: 130px;
    }
    .timer {
        font-size: 60px;
        margin-bottom: 20px;
    }
    .scroll {
        position: absolute;
        bottom: 14px;
        width: 13px;
        height: 20px;
        animation: scroll 2s ease-in-out infinite;
        &::before,
        &:after {
            content: "";
            position: absolute;
            top: 0;
            height: 100%;
            width: 2px;
            background-color: white;
        }
        &::before {
            box-shadow: 0 1px 1px 3px hsla(209, 100%, 8%, 0.329);
            left: 0;
            transform: rotate(145deg);
        }
        &:after {
            box-shadow: 0 1px 1px 5px hsla(209, 100%, 8%, 0.329);
            right: 0;
            transform: rotate(-145deg);
        }

        @keyframes scroll {
            0% {
                transform: translate(0, 10px);
            }
            50% {
                transform: translate(0, 0);
            }
            100% {
                transform: translate(0, 10px);
            }
        }
    }
}
