* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
}
html {
    scroll-behavior: smooth;
}

.container {
    display: grid;
    grid-template-columns: 200px 1fr;
    grid-template-areas: "navbar" "mainWallper";

    & .navBar {
        grid-area: "navbar";
        position: sticky;
        top: 0;
        left: 0;
        width: 200px;
        height: 100vh;
    }

    & .mainWallper {
        grid-area: "mainWallper";
        overflow: hidden;

        & .containerContent {
            padding: 25px 50px;
            margin: 0 auto;
        }
    }
}
