#footer {
    height: fit-content;
    gap: 20px;
    text-align: center;
    padding-bottom: 0;
    background-color: rgb(21, 29, 32);

    & .contactBlock {
        height: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 10px;

        & .footerText {
            width: 50%;
            max-height: 450px;

            display: flex;
            flex-direction: column;
            justify-content: center;

            font-size: 20px;
            color: white;
            & .contactText {
                & p:nth-of-type(1) {
                    color: rgb(253, 160, 51);
                    font-weight: bold;
                    padding-bottom: 5px;
                }

                & p:nth-child(2) a {
                    color: white;
                    text-decoration: none;

                    &:hover {
                        border-bottom: 1px solid white;
                    }
                }
            }
            & .contactAdress {
                margin-top: 20px;
                & p:nth-of-type(1) {
                    color: rgb(253, 160, 51);
                    font-weight: bold;
                    padding-bottom: 5px;
                }
            }
        }
    }

    & .signature {
        color: white;
        font-size: 14px;

    }
}
